<template>
  <div id="monitor" style="height: 100vh;">
    <div class="header">
      <h1 style="color: #fff;">QUEUEING MANAGEMENT SYSTEM</h1>
    </div>
    <div class="content">
      <div class="ads">
        <div class="video-ads">
          <iframe width="100%" height="100%" src="https://www.youtube.com/embed/tgbNymZ7vqY">
          </iframe>
        </div>
      </div>
      <div class="counters">
        <div v-for="(counter, index) in counters" :key="index">
          <div class="card" :class="{ 'green': index === 0, 'orange': index === 1, 'blue-gray': index === 2 }">
            <div class="header bg-red">
                <h2 style="font-size: 35px;">
                    {{ counter.label }}
                </h2>
            </div>
            <div class="body text-center">
                <h1 style="font-size: 65px; margin-top: 0px; margin-bottom: 0px;" :id="counter._id">N/A</h1>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
import moment from 'moment'
export default {
	data () {
		return {
			date: moment().format('LLL'),
      counters: []
		}
	},
  async mounted () {
    // try {
    //   await isAuth()
    // } catch (error) {
    //   console.log(error)
    // }
    this.getCounters()
    this.getNowServing()
  },
  methods: {
    async getCounters () {
      try {
        let result = await app.service('counters').find()
        this.counters = result.data
      } catch (error) {
        console.log(error)
      }
    },
    async getNowServing () {
      try {
        let result = await app.service('now-serving').find()
        console.log('pota', result)
        // console.log('now-serving',result.data)
        result.data.forEach(val => {
          $(`#${val.counter._id}`).text(val.ticket.label)
        })
      } catch (error) {
        console.log(error)
      }
    },
    async getTicket (ticket) {
      console.log('ticket id', ticket)
      try {
        let result = await app.service('tickets').get(ticket.ticket)
        $(`#${ticket.counter}`).text(result.label)
      } catch (error) {
        console.log(error)
      }
    }
  },
  feathers: {
    'now-serving': {
      created (ticket) {
        this.getTicket(ticket)
      }
    }
  }
}
</script>
<style scoped>
#monitor {
  display: flex;
  flex-direction: column;
}
.header {
  background: #F4453E;
  height: 10vh;
  padding-left: 20px;
}
.content {
  background: #E9E9E9;
  height: 90vh;
  display: flex;
}
.ads {
  width: 75%;
  padding: 20px;
  padding-right: 0px;
}
.counters {
  width: 25%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  /*height: 90vh;*/
  /*overflow-y: scroll;*/
}
.video-ads {
  /*background: red;*/
  height: 100%;
}
.mid-counter {
  background: #89e8c5 !important;
}
.green > .header {
  background: #4DAE55 !important;
}
.orange > .header {
  background: #FF9729 !important;
}
.blue-gray > .header {
  background: #607D8A !important;
}
</style>


