<template>
	<div id="advertisements">
		<section class="content">
			<div class="container-fluid">
			  <div class="block-header">
					<div class="row clearfix">
				    <div class="col-lg-12 col-md-12 col-sm-12 col-xs-12">
			        <div class="card">
		            <div class="header">
	                <h2>ADVERTISEMENTS</h2>
	                <ul class="header-dropdown m-r--5">
                    <li class="dropdown">
                      <a href="javascript:void(0);" class="dropdown-toggle" data-toggle="dropdown" role="button" aria-haspopup="true" aria-expanded="false">
                        <i class="material-icons">more_vert</i>
                      </a>
                      <ul class="dropdown-menu pull-right">
                        <li><a data-toggle="modal" data-target="#addAdvertisements" class=" waves-effect waves-block">Add New Advertisement</a></li>
                      </ul>
                    </li>
	                </ul>
		            </div>
		            <div class="body table-responsive">
	                <table class="table">
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>NAME</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(advertisement, index) in 10" :key="index">
                        <th scope="row">{{ index+1 }}</th>
                        <td>{{ 'name' }}</td>
                        <td>enable/disable button here<td>
                        	<i class="material-icons" data-toggle="modal" data-target="#editAdvertisements" style="cursor: pointer;">mode_edit</i>
                        	<i class="material-icons" style="cursor: pointer;">delete</i>
                        </td>
                      </tr>
                    </tbody>
	                </table>
		            </div>
			        </div>
				    </div>
					</div>			  
				</div>
			</div>
		</section>
		<!-- Create Advertisement -->
		<div class="modal fade" id="addAdvertisements" tabindex="-1" role="dialog">
		  <div class="modal-dialog" role="document">
				<div class="modal-content">
				  <div class="modal-header">
				      <h4 class="modal-title">Add New Advertisement</h4>
				  </div>
				  <form>
				  	<div class="progress">
              <div class="progress-bar" role="progressbar" :aria-valuenow="0"
              	aria-valuemin="0" aria-valuemax="100" :style="'width: ' + currentFileUploadProgress + '%;'">
                <!-- <span class="sr-only">60% Complete</span> -->
              </div>
            </div>
            
            <div class="progress-bar" role="progressbar"></div>

					  <div class="modal-body" style="padding-bottom: 0px;">
							<div class="form-group form-float">
							  <div class="form-line">
							      <input type="text" class="form-control">
							      <label class="form-label">Name:</label>
							  </div>
							</div>
							<input type="file" @change="uploadFile">
					  </div>
					  <div class="modal-footer" style="padding-top: 0px;">
							<button type="submit" class="btn btn-link waves-effect">
							<span>SAVE</span>
							</button>
							<button type="button" class="btn btn-link waves-effect" data-dismiss="modal">CLOSE</button>
					  </div>
				  </form>
				</div>
		  </div>
		</div>
		<!-- Edit Counter -->
		<div class="modal fade" id="editAdvertisements" tabindex="-1" role="dialog">
		  <div class="modal-dialog" role="document">
				<div class="modal-content">
				  <div class="modal-header">
				      <h4 class="modal-title">Edit Counter</h4>
				  </div>
				  <form>
					  <div class="modal-body" style="padding-bottom: 0px;">
							  <label for="first_name">Name:</label>
							  <div class="form-group">
							      <div class="form-line">
							          <input type="text" class="form-control">
							      </div>
							  </div>
					  </div>
					  <div class="modal-footer" style="padding-top: 0px;">
					      <button type="submit" class="btn btn-link waves-effect">
					      <span>SAVE</span>
					    	</button>
					      <button type="button" class="btn btn-link waves-effect" data-dismiss="modal">CLOSE</button>
					  </div>
				  </form>
				</div>
		  </div>
		</div>
	</div>
</template>
<script>
import app from '@/api'
import isAuth from '@/api/isAuth'
import { API_SERVER } from '@/api/url'
import moment from 'moment'

export default {
	data () {
		return {
			newAdvertisement: {},
			advertisements: [],
			currentFileUploadProgress: 0
		}
	},
	mounted () {
		this.validateUser()

		const reader = new FileReader();

		/**
		 * Upload Input On Change
		 */
		$('#upload-input').on('change', function(){

		  var files = $(this).get(0).files;

		  if (files.length > 0){
		    // create a FormData object which will be sent as the data payload in the
		    // AJAX request
		    var formData = new FormData();

		    // loop through all the selected files and add them to the formData object
		    for (var i = 0; i < files.length; i++) {
		      var file = files[i];

		      // add the files to formData object for the data payload
		      formData.append('uploads[]', file, file.name);
		    }

		    $.ajax({
		      url: `${API_SERVER}/file-upload`,
		      type: 'POST',
		      data: formData,
		      processData: false,
		      contentType: false,
		      success: function(data){
	          console.log('upload successful!\n' + JSON.stringify(data));
		      },
		      
		    });

		  }
		});

	},
	methods: {
		async validateUser () {
			try {
				let result = await isAuth()
				if (result.user.type !== 'superuser') {
					window.location.href = '/login'
				}
			} catch (error) {
				console.log(error)
			}
		},
		uploadFile (event) {
			this.currentFileUploadProgress = 0
			let file = event.target.files[0];
      let formData = new FormData();

      formData.append('uri', file);

      // $('#submitForm').attr('disabled', true);
      // let self = this;

      $.ajax({
        url: `${API_SERVER}/file-upload`,
        cache: false,
        contentType: false,
        processData: false,
        data: formData,
        type: 'POST',
        success (response) {
          console.log(response)
        },
        error (error) {
          console.log(error);
        },
        xhr: function() {
	        // create an XMLHttpRequest
	        var xhr = new XMLHttpRequest();

	        // listen to the 'progress' event
	        xhr.upload.addEventListener('progress', (evt) => {
	          if (evt.lengthComputable) {
	            // calculate the percentage of upload completed
	            var percentComplete = evt.loaded / evt.total;
	            percentComplete = parseInt(percentComplete * 100);

	            this.currentFileUploadProgress = percentComplete

	            // once the upload reaches 100%, set the progress bar text to done
	            if (percentComplete === 100) {
	              alert('Upload complete!')
	            }
	          }
	        }, false);

	        return xhr;
	      } // xhr
    	});
		} // uploadFile
	} // methods
}
</script>


